.App-link {
  color: #61dafb;
}


.header { 
  background-color: #282c34;
  height: 80px;
  display: -webkit-flex;
  display: flex;
  text-align: center;
  color: white;
  padding: 15px;
  font-size: calc(10px + 2vmin);
}

.header__store-title { 
  -webkit-flex: 1 0 auto; 
          flex: 1 0 auto;
}

.header__icon { 
  padding-top: 10px;
}



.product{
  -webkit-flex: 1 0 30vh;
          flex: 1 0 30vh;
  margin: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: space-between;
          justify-content: space-between
}
.product__image { 
  width: 100%;
}

.collection__product-container{ 
  display: -webkit-flex; 
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}
